(function() {
	'use strict';

	angular.module('News', [
			'ngRoute'
		])
		.config(routeConfig);

	function routeConfig($routeProvider) {
		$routeProvider
			.when('/news', {
				templateUrl: 'app/news/news.html',
				controller: 'newsCtrl',
				controllerAs: 'newsController'
			});
	}
})();

(function() {
	'use strict';
	/**
	 * @ngdoc function
	 * @name News.controller:newsCtrl
	 * @description
	 * # newsCtrl
	 * Controller of the Main
	 */
	angular.module('News')
		.controller('newsCtrl', function ($scope) {
			$scope.awesomeThings = [
				'HTML5 Boilerplate',
				'AngularJS',
				'Karma'
			];
		});
})();
