(function() {
  'use strict';

  angular
    .module('Main', [
		  'Blog',
		  'Events',
		  'News',
		  'ngAnimate',
		  'ngAria',
		  'ngCookies',
		  'ngMaterial',
		  'ngMessages',
		  'ngResource',
		  'ngRoute',
		  'ngSanitize',
		  'ngTouch',
		  'toastr'
	    ])
		.config(function($mdThemingProvider) {
			$mdThemingProvider.theme('default')
				.primaryPalette('blue')
				.accentPalette('orange')
		        .dark();
		});

})();

(function() {
  'use strict';

  angular
    .module('Main')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {},
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController() {
    }
  }

})();

